<template>
  <!--  -->
  <!-- <Spin_C /> -->
  <!--  -->
  <div class="container">
    <div class="test">
      <!--  -->
      <div class="hero">
        <!--  -->
        <div class="head">
          <p class="title">Task Manager</p>
        </div>
        <!--  -->
        <div class="ebox">
          <div v-if="!onLine" class="pop">
            <div class="msg">Connection Lost</div>
          </div>
          <div v-if="stop" class="stopbox" v-show="stop.length > 0">
            <li v-for="(error, index) in stop" :key="index">{{ error }}</li>
          </div>
        </div>
        <!--  -->
        <div class="io">
          <div class="create-post">
            <label for="create-post"></label>
            <input
              v-model="text"
              type="text"
              id="create-post"
              placeholder="Enter Task :"
              class="ip"
              v-on:keyup.enter="createPost"
            />
            <button class="btn" @click="createPost">Enter Task</button>
          </div>
          <p class="error" v-if="error">{{ error }}</p>
        </div>
      </div>
      <!--  -->
      <hr class="divide" />
      <div class="post-container">
        <!-- v-bind:index="index" -->
        <div
          class="post"
          v-for="post in posts"
          v-bind:item="post"
          v-bind:key="post._id"
        >
          <!-- v-on:dblclick="deletePost(post._id)" -->
          <div class="content">
            <p
              :class="[post.rem ? '' : 'text-s', 'text']"
              @click="post.rem = !post.rem"
            >
              {{ post.text }}
            </p>
          </div>
          <div class="options">
            <button class="Ubtn" @click="updation()">Update</button>
            <button class="Dbtn" @click="deletePost(post._id)">Delete</button>
          </div>
        </div>
      </div>
    </div>
    <div class="foot">Foot</div>
  </div>
</template>

<script>
import PostService from '../PostService'
// import JwPagination from 'jw-vue-pagination'
// import Spin_C from '../components/Spin_C.vue'

export default {
  name: 'PostComponent',
  inject: ['spinstate'],
  data() {
    return {
      posts: [],
      stop: [],
      error: '',
      text: '',
      onLine: navigator.onLine ? true : false,
      toggle: false,
      flag: false,
    }
  },
  async created() {
    try {
      this.spinstate.value = true
      this.posts = await PostService.getPosts()
      // console.log(this.posts)
      this.spinstate.value = false
    } catch (err) {
      this.error = err.message
    }
    setInterval(() => {
      this.checkOnline()
    }, 30000)
  },
  methods: {
    updation() {
      console.log('update()')
      this.flag = true
    },
    async createPost() {
      this.stop = []
      if (!this.text) {
        // console.log('expty string')
        this.stop.push('Empty Task Invalid')
      } else {
        this.spinstate.value = true
        await PostService.insertPost(this.text)
        this.text = ''
        this.posts = await PostService.getPosts()
        console.log(this.stop)
        this.spinstate.value = false
      }
    },
    async deletePost(id) {
      this.spinstate.value = true
      await PostService.deletePost(id)
      this.posts = await PostService.getPosts()
      this.spinstate.value = false
    },
    checkOnline() {
      this.onLine = navigator.onLine ? true : false
      if (this.onLine) {
        this.stop = []
      } else {
        this.stop.push('System Offline !')
      }
    },
  },
  // components: { 'jw-pagination': JwPagination },
  // Spin_C
}
</script>

